export interface SiteScript {
  /**
   * Position where the script is placed on the page.
   * @readonly
   */
  position?: PositionOnPageWithLiterals;
  /**
   * Script HTML
   * @readonly
   * @maxLength 15000
   */
  html?: string;
}

export enum PositionOnPage {
  UNKNOWN_POSITION = 'UNKNOWN_POSITION',
  /** HEAD position */
  HEAD = 'HEAD',
  /** BODY_START position */
  BODY_START = 'BODY_START',
  /** BODY_END position */
  BODY_END = 'BODY_END',
}

/** @enumType */
export type PositionOnPageWithLiterals =
  | PositionOnPage
  | 'UNKNOWN_POSITION'
  | 'HEAD'
  | 'BODY_START'
  | 'BODY_END';

export interface ListSiteScriptsRequest {}

export interface ListSiteScriptsResponse {
  /** List of site scripts. */
  siteScripts?: SiteScript[];
}

export interface GetEssentialPropertiesRequest {}

export interface GetEssentialPropertiesResponse {
  /** Properties of the site */
  properties?: EssentialProperties;
}

export interface EssentialProperties {
  /** Site locale. */
  locale?: Locale;
  /**
   * Site language.
   * Two-letter language code in [ISO 639-1 alpha-2](https://en.wikipedia.org/wiki/List_of_ISO_639-1_codes) format.
   * @format LANGUAGE
   */
  language?: string | null;
  /**
   * Site currency format used to bill customers.
   * Three-letter currency code in [ISO-4217 alphabetic](https://en.wikipedia.org/wiki/ISO_4217#Active_codes) format.
   * @format CURRENCY
   */
  paymentCurrency?: string | null;
  /**
   * Timezone in `America/New_York` format.
   * @maxLength 50
   */
  timeZone?: string | null;
}

export interface Locale {
  /**
   * Two-letter language code in [ISO 639-1 alpha-2](https://en.wikipedia.org/wiki/List_of_ISO_639-1_codes) format.
   * @format LANGUAGE
   */
  languageCode?: string;
  /**
   * Two-letter country code in [ISO-3166 alpha-2](https://en.wikipedia.org/wiki/ISO_3166-1_alpha-2#Officially_assigned_code_elements) format.
   * @format COUNTRY
   */
  country?: string;
}
